<template>
	<div id="user">
		<!-- 对话框 -->
		<el-dialog :title="etxt?'新增用户':'修改用户'" :visible.sync="isshow">
			<userfrom :form="userfroms" :formLabel="userfromlabel"></userfrom>
			<div slot="footer" class="dialog-footer">
				<el-button @click="isshow = false">取 消</el-button>
				<el-button type="primary" @click="kfrom">确 定</el-button>
			</div>
		</el-dialog>


		<div id="title">
			<div style="height:50px;">
				<el-button type="primary" @click="adduser">+新增</el-button>
			</div>
			<div class="liftTitle">
				<userfrom :formLabel="formLabel" :form="searchForm" inline>
					<el-button type="primary" style="height: 40px;" @click="getUserList(searchForm.keyword)">搜索
					</el-button>
				</userfrom>

			</div>

		</div>
		<commonTable :tableData="tableData" :tableLabel="tableLabel" :config="config" @changepage="getUserList()"
			@commt="commt" @upduser="deluser">
		</commonTable>

	</div>
</template>

<script>
	import userfrom from '@/components/userfrom.vue'
	import commonTable from '@/components/commonTable.vue'
	export default {
		components: {
			userfrom,
			commonTable

		},
		data() {
			return {
				etxt: 'true',
				isshow: false,
				userfroms: { //表单数据
					name: '',
					sex: '',
					birth: '',
					addr: '',
					age: ''
				},
				userfromlabel: [ //表单数据
					{
						data: 'name',
						label: '姓名',
						type: 'input'
					}, {
						data: 'sex',
						label: '性别',
						type: 'select',
						options: [{
								label: '女',
								value: 0
							},
							{
								label: '男',
								value: 1
							}
						]
					}, {
						data: 'birth',
						label: '出生日期',
						type: 'date'
					}, {
						data: 'addr',
						label: '地址',
						type: 'input'
					}, {
						data: 'age',
						label: '年龄',
						type: 'input'
					}
				],
				tableData: [],
				tableLabel: [{
						label: '姓名',
						prop: 'name'
					},
					{
						label: '年龄',
						prop: 'age'
					},
					{
						label: '性别',
						prop: 'sexLabel'
					},
					{
						label: '出生日期',
						prop: 'birth'
					},
					{
						label: '地址',
						prop: 'addr'
					},
				],
				config: {
					page: 1,
					total: 20
				},
				searchForm: {
					keyword: ''
				},
				formLabel: [{
					data: 'keyword', //用户在输入框输入的数据
					label: '', //数据标签
					type: 'input' //类型
				}],

			}
		},
		methods: {
			getUserList(name = '') {
				console.log(name)
				name ? (this.config.page = 1) : ''
				this.$http.get('/api/user/getUserData', {
					params: {
						page: this.config.page,
						name
					}
				}).then(res => {
					//返回接口返回的总条数
					this.config.total = res.data.count
					this.tableData = res.data.list.map(item => {
						item.sexLabel = item.sex === 0 ? '女' : '男'
						return item
					})
					// console.log(111, this.config.total)
				})
			},
			adduser() { //新增用户
				this.isshow = true
				this.etxt = true
				this.userfroms = {}
			},
			commt(row) { //修改用户
				this.isshow = true
				this.etxt = false
				//先拷贝row对象
				let new_row = {
					...row
				}
				this.userfroms = new_row
				// console.log(row)
			},
			deluser(row) { //删除
			        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
			          confirmButtonText: '确定',
			          cancelButtonText: '取消',
			          type: 'warning'
			        }).then(() => {
						this.$http.get('/api/user/delUserInfo', {
							params: {
								id: row.id
							}
						}).then(res => {
							this.getUserList() //重新加载用户列表
						})
			          this.$message({
			            type: 'success',
			            message: '删除成功!'
			          });
			        }).catch(() => {
			          this.$message({
			            type: 'info',
			            message: '已取消删除'
			          });          
			        });
			
			
			
				
			},
			kfrom() { //点击确定
				// 判断是添加还是修改
				if (this.etxt) { //添加
					this.$http.post('/api/user/addUserInfo', this.userfroms).then(res => {
						this.isshow = false
						this.getUserList() //重新加载用户列表
					})

				} else {
					//修改
					this.$http.post('/api/user/updUserInfo', this.userfroms).then(res => {
						this.isshow = false
						this.getUserList() //重新加载用户列表
					})
				}
			}
		},
		mounted() {
			this.getUserList()
		}
	}
</script>

<style lang="scss" scoped>
	// @import '~@/styles/common.scss';
	#title {
		display: flex;
		justify-content: space-between;
	}

	.liftTitle {
		display: flex;
		justify-content: space-between;
	}
</style>
