<template>
	<el-form ref="form" :model="form" label-width="100px" :inline='inline'>
	   <!-- 
	  v-model:绑定表单的字段 --从父组件传递过来的
	    label:表单描述标签
	    -->
	   <el-form-item v-for="item in formLabel" :label="item.label" :key="item.data">
	    <!-- 文本框 -->
	    <el-input v-model="form[item.data]" v-if="item.type==='input' " :placeholder="'请输入'+item.label">
	    </el-input>
	
	    <!-- 日期选择器 -->
	    <el-date-picker type="date" placeholder="选择日期" v-model="form[item.data]" style="width: 100%;"
	     v-if="item.type==='date'" value-format="yyyy-MM-dd"></el-date-picker>
	
	    <!-- 下拉框 -->
	    <el-select v-model="form[item.data]" :placeholder="'请输入'+item.label" v-if="item.type==='select'">
	     <el-option v-for="op in item.options" :label="op.label" :value="op.value"></el-option>
	
	    </el-select>
	   </el-form-item>
	
	
	   <!-- 搜索框 -->
	   <el-form-item>
	    <slot></slot>
	   </el-form-item>
	
	  </el-form>
</template>
<script>
  export default {
   props:{//接收父组件数据
	   form:Object,//表单数据
	   formLabel:Array,//数据标签
	   inline:Boolean,//是否是行内表单
   },
  }
</script>